.main {
  display: flex;
  flex-direction: column;
  padding-block: 64px;
  
  @include contentPaddindngInline;
  @include contentPaddindngBlock;

  transition: padding-block $effectDuration,  padding-inline $effectDuration;

  &__section:nth-child(1) {
    margin-bottom: 96px;
    transition: margin-bottom $effectDuration;

    @include onTablet {
      margin-bottom: 120px;
    }

    @include onDesktop {
      margin-bottom: 128px;
    }
  }

  &__section:nth-child(2) {
    margin-bottom: 48px;
    transition: margin-bottom $effectDuration;

    @include onTablet {
      margin-bottom: 56px;
    }
  }

  &__section:nth-child(3) {
    margin-bottom: 104px;
    transition: margin-bottom $effectDuration;

    @include onTablet {
      margin-bottom: 120px;
    }
  }

}
