
.section-title {
  margin: 0;
  margin-bottom: 48px;
  font-size: 32px;
  font-weight: 700;
  line-height: 100%;
  text-align: left;

  &:hover {
    @include hover(transform, scale(1.03));
  }

  @include onTablet {
    margin-bottom: 56px;
    font-size: 40px;
  }

  &--About-Us {
    margin-bottom: 24px;

    @include onTablet {
      margin-bottom: 0px;
    }
  }

  @include onDesktop {
    font-size: 48px;
  }
}
